export default {
    name: "modal",
    data(){
        return{
            show:false
        }
    },
    props:{
       title:{
           type:String,
           default:'标题'
       },
       value:{
           type:Boolean,
           default: false
       }
    },
    computed:{

    },
    watch:{
        value(val){
            this.show = val
        }
    },
    methods:{
        hideModal(){
            this.show = false;
            this.$emit("input",this.show)
        },
        onConfirm(){
            this.$emit('onConfirm')
        },
        onCancel(){
            this.$emit('onCancel')
        }

    }
}
